<template>
    <div>
        <b-card >
            <template #header>
                <h2 class="mb-0">Edit User</h2>
            </template>
            <b-card-body>
                <b-form ref="form" @submit.prevent="submit()">
                    <b-row>
                        <b-col sm="6" class="mt-2">
                            <label class="pb-1 d-block">Role</label>
                            <b-form-select :options="userRoles" text-field="text" value="value" v-model="info.alias">
                                <b-select-option hidden :value="undefined"></b-select-option>
                            </b-form-select>
                        </b-col>

                        <b-col sm="6" class="mt-2">
                            <label class="pb-1 d-block">First Name</label>
                            <b-input placeholder="First Name" required v-model="info.first_name"></b-input>

                        </b-col>
                        
                        <b-col sm="6" class="mt-2">
                            <label class="pb-1 d-block">Last Name</label>
                            <b-input placeholder="Last Name" required v-model="info.last_name"></b-input>
                        </b-col>



                        <b-col sm="6" class="mt-2">
                            <label class="pb-1 d-block">Email</label>
                            <b-input placeholder="Email" required type="email" v-model="info.email"></b-input>
                        </b-col>

                        <b-col sm="6" class="mt-2">
                            <label class="pb-1 d-block" >Password</label>
                            <b-input placeholder="Password"  type="password" v-model="info.password"></b-input>
                        </b-col>

                        <b-col sm="6" class="mt-2">
                            <label class="pb-1 d-block" >Confirm Password</label>
                            <b-input placeholder="Confirm Password"  type="password" v-model="info.password_confirmation"></b-input>
                        </b-col>

                        <b-col sm="6" class="mt-2">
                            <label class="pb-1 d-block" >Phone</label>
                            <b-input placeholder="Phone" required type="tel" v-model.number="info.phone"></b-input>
                        </b-col>

                        <!--countries-->
                        <template>
                            <b-col sm="12">
                                <b-row>
                                    <b-col sm="4" class="mt-2">
                                        <label>Country</label>
                                        <b-form-select  v-model="info.country_id" :options="countries" text-field="name" value-field="id">
                                            <b-form-select-option value="">Select Country</b-form-select-option>
                                        </b-form-select>
                                    </b-col>
                        
                                    <b-col sm="4" class="mt-2">
                                        <label>States</label>
                                        <b-form-select  :disabled="states.length < 1" v-model="info.state_id" :options="states"  text-field="name" value-field="id"> 
                                            <b-form-select-option value="">Select State</b-form-select-option>
                                        </b-form-select>
                                    </b-col>

                                    <b-col sm="4" class="mt-2">
                                        <label>States</label>
                                        <b-form-select  :disabled="cities.length < 1" v-model="info.city_id" :options="cities"  text-field="name" value-field="id"> 
                                            <b-form-select-option value="">Select City</b-form-select-option>
                                        </b-form-select>
                                    </b-col>
                                </b-row>
                            </b-col>
                        </template>

                        <div class="d-flex w-100">
                            <b-button variant="success" :disabled="loading.submit" type="submit" class="w-25 mt-3 ml-auto mr-3">
                                <span v-if="!loading.submit">Submit</span>
                                <b-spinner v-if="loading.submit" small type="grow"></b-spinner>
                            </b-button>
                        </div>
                    </b-row>
                </b-form>
            </b-card-body>
            
        </b-card>
        
    </div>
</template>
<script>
import axios from "axios"

import { showToast } from '@/assets/js/index';

export default {
    name:"edit",
    data() {
        return {
            info:{},

            userRoles:[
                {
                    text:"Admin",
                    value:1
                },
                {
                    text:"Delivery",
                    value:2
                },
                {
                    text:"User",
                    value:3
                }
            ],
            loading:{
                submit:false
            }
        }
    },
    computed:{
        countries() {
            return this.$store.state.countries
        },
        states() {
            return this.$store.state.states
        },
        cities() {
            return this.$store.state.cities
        }
    },
    methods:{
        submit() {
            this.loading.submit = true;
            let data = {
                first_name: this.info.first_name,
                last_name:  this.info.last_name,
                phone:      this.info.phone,
                email:      this.info.email,
                country_id: this.info.country_id,
                state_id:   this.info.state_id,
                city_id:    this.info.city_id
                
            }
            if(this.info.alias) {
                data.alias = this.info.alias
            }
            if (this.info.password_confirmation) {
                data.password               = this.info.password;
                data.password_confirmation  = this.info.password_confirmation
            }
            axios.put(`/admin/v1/users/${this.$route.params.id}`, data)
            .then(res=>{
                console.log(res)
                this.loading.submit = false;
                showToast('success', 'Success', 'User has been updated successfully!', this)
       
    
            })
            .catch(err=>{
                this.loading.submit = false;
                for(let error in err.response.data.errors) {
                    showToast('danger', `${error}`, `${err.response.data.errors[error][0]}`, this)
                }
                console.log(err.response)
            })
        }
    },
  watch:{
        'info.country_id': function() {
            //to prevent this function in reset mode
            if(this.info.country_id) {
                this.$store.dispatch('getStates', this.info.country_id);
                //get selected country
                let selectedCountry = this.countries.find(country=>{
                    return country.id == this.info.country_id
                })
                this.info.phone_code = selectedCountry.phonecode
            }

        },
        'info.state_id':function() {
            //to prevent this function in reset mode
            if(this.info.state_id) {
                this.$store.dispatch('getCities', this.info.state_id)
            }
        }
    },
    mounted(){
        this.$store.dispatch('getCountries')
        //get data
        axios.get(`admin/v1/users/${this.$route.params.id}`)
        .then(res=>{
            this.info = res.data.data
        })
    }
}
</script>